export default {
	data() {
		return {
			// 为了适配小程序 将顶部背景图转成base64
			background: img,
			userName:uni.getStorageSync("user_name"),
			userIcon: uni.getStorageSync("user_icon"),
			userGroup:uni.getStorageSync("user_group"),
			day:'0',
			month:'0',
			week:'0'
		}
	},
	methods:{
		// 暂时不用
		handleTimeButton(){
